<template>
  <div class="folder">
    <h2
      class="folder__title"
      v-bind:class="{ 'folder__title--open': isFolderNotEmpty }"
    >
      {{ name }}
    </h2>

    <ul class="folder__list">
      <li
        class="folder__folder-item"
        v-for="(folder, index) in folders"
        v-bind:key="index"
      >
        <FolderView
          :name="folder.name"
          :folders="folder.folders"
          :files="folder.files"
        />
      </li>

      <li
        class="folder__file-item"
        v-for="(file, index) in files"
        v-bind:key="index"
      >
        <FileView :name="file.name" />
      </li>
    </ul>
  </div>
</template>

<script>
import FileView from "./FileView";

export default {
  name: "FolderView",
  components: { FileView },
  props: {
    name: String,
    folders: Array,
    files: Array,
  },
  computed: {
    isFolderNotEmpty() {
      return this.folders.length || this.files.length;
    },
  },
};
</script>

<style scoped lang="scss">
ul {
  margin: 0;
  list-style-type: none;
}

.folder {
  padding: 10px;
  border: 1px solid #dec0c0;
  border-radius: 5px;
  background-color: rgba(80, 111, 89, 0.231);

  &__title {
    position: relative;
    display: inline-block;
    margin-top: 0;
    padding-left: 30px;
    color: #ff8178;
    font-size: 19px;
    font-weight: 600;
    background-image: url("../../public/img/folder.png");
    background-position: left;
    background-size: 20px;
    background-repeat: no-repeat;

    &--open::after {
      position: absolute;
      top: 50%;
      transform: rotateZ(90deg);
      left: 110%;
      content: "";
      width: 20px;
      height: 20px;
      background-image: url("../../public/img/arrow.png");
      background-size: cover;
      background-repeat: no-repeat;
    }
  }

  &__folder-item:not(:last-of-type),
  &__file-item:not(:last-of-type) {
    margin-bottom: 20px;
  }
}
</style>
